import React, { useState, useEffect, useRef } from "react";

/*
 * LandingV2.jsx - Conversion-Optimized Landing Page
 *
 * Testing Checklist:
 * - [ ] Renders without errors
 * - [ ] No horizontal scroll on mobile
 * - [ ] All CTAs trigger startAssessment
 * - [ ] Calculator math is correct
 * - [ ] Works in Safari, Chrome, Firefox
 * - [ ] No console errors
 * - [ ] Loads in under 2 seconds
 */

const LandingV2 = ({ startAssessment }) => {
  const [teamSize, setTeamSize] = useState(5);
  const [hoursLost, setHoursLost] = useState(8);
  const [hourlyRate, setHourlyRate] = useState(85);
  const [buttonHovered, setButtonHovered] = useState(false);
  const [linkHovered, setLinkHovered] = useState(false);
  const [showStickyCTA, setShowStickyCTA] = useState(false);
  const [visibleSections, setVisibleSections] = useState(new Set());
  const [pulseAnimation, setPulseAnimation] = useState(false);

  const heroRef = useRef(null);
  const sectionRefs = useRef({});

  const calculateCost = () => {
    return teamSize * hoursLost * hourlyRate * 52;
  };

  // Pulse animation when calculator updates
  useEffect(() => {
    setPulseAnimation(true);
    const timer = setTimeout(() => setPulseAnimation(false), 600);
    return () => clearTimeout(timer);
  }, [teamSize, hoursLost, hourlyRate]);

  // Scroll animations and sticky CTA
  useEffect(() => {
    const handleScroll = () => {
      // Check if hero is scrolled past
      if (heroRef.current) {
        const heroBottom =
          heroRef.current.offsetTop + heroRef.current.offsetHeight;
        setShowStickyCTA(window.scrollY > heroBottom);
      }

      // Check visible sections for fade-in
      const newVisibleSections = new Set();
      Object.values(sectionRefs.current).forEach((ref) => {
        if (ref) {
          const rect = ref.getBoundingClientRect();
          const isVisible =
            rect.top < window.innerHeight * 0.8 && rect.bottom > 0;
          if (isVisible) {
            newVisibleSections.add(ref.id);
          }
        }
      });
      setVisibleSections(newVisibleSections);
    };

    window.addEventListener("scroll", handleScroll);
    handleScroll(); // Initial check
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // Smooth scroll for anchor links
  useEffect(() => {
    const handleClick = (e) => {
      const target = e.target.closest('a[href^="#"]');
      if (target) {
        e.preventDefault();
        const targetId = target.getAttribute("href").substring(1);
        const targetElement = document.getElementById(targetId);
        if (targetElement) {
          targetElement.scrollIntoView({
            behavior: "smooth",
            block: "start",
          });
        }
      }
    };

    document.addEventListener("click", handleClick);
    return () => document.removeEventListener("click", handleClick);
  }, []);

  // Handle CTA clicks
  const handleCTAClick = (e) => {
    e.preventDefault();
    if (startAssessment) {
      startAssessment();
    }
  };

  const styles = {
    container: {
      fontFamily: "Inter, -apple-system, BlinkMacSystemFont, sans-serif",
      lineHeight: "1.6",
      color: "#333",
      maxWidth: "600px",
      margin: "0 auto",
      padding: "20px",
      backgroundColor: "#fafafa",
      minHeight: "100vh",
      boxSizing: "border-box",
    },
    hero: {
      textAlign: "center",
      marginBottom: "32px",
      padding: "32px 20px",
      backgroundColor: "white",
      borderRadius: "8px",
      boxSizing: "border-box",
      opacity: 1,
      transform: "translateY(0)",
      transition: "opacity 0.6s ease, transform 0.6s ease",
    },
    heroHidden: {
      opacity: 0,
      transform: "translateY(20px)",
    },
    headline: {
      fontSize: "clamp(24px, 5vw, 32px)",
      fontWeight: "600",
      color: "#111",
      marginBottom: "16px",
      lineHeight: "1.2",
      textTransform: "lowercase",
      wordWrap: "break-word",
    },
    subhead: {
      fontSize: "clamp(16px, 3.5vw, 18px)",
      fontWeight: "400",
      color: "#666",
      marginBottom: "0",
      lineHeight: "1.4",
      textTransform: "lowercase",
    },
    stats: {
      display: "grid",
      gridTemplateColumns: "repeat(auto-fit, minmax(140px, 1fr))",
      gap: "16px",
      marginBottom: "32px",
      opacity: 1,
      transform: "translateY(0)",
      transition: "opacity 0.6s ease, transform 0.6s ease",
    },
    statsHidden: {
      opacity: 0,
      transform: "translateY(20px)",
    },
    statCard: {
      padding: "20px 16px",
      backgroundColor: "white",
      borderRadius: "8px",
      textAlign: "center",
      border: "1px solid #e5e5e5",
      boxSizing: "border-box",
    },
    statNumber: {
      fontSize: "clamp(20px, 4vw, 24px)",
      fontWeight: "600",
      color: "#00c9a7",
      marginBottom: "8px",
    },
    statText: {
      fontSize: "clamp(12px, 2.5vw, 14px)",
      color: "#666",
      textTransform: "lowercase",
      wordWrap: "break-word",
    },
    process: {
      marginBottom: "32px",
      padding: "24px 20px",
      backgroundColor: "white",
      borderRadius: "8px",
      border: "1px solid #e5e5e5",
      boxSizing: "border-box",
      opacity: 1,
      transform: "translateY(0)",
      transition: "opacity 0.6s ease, transform 0.6s ease",
    },
    processHidden: {
      opacity: 0,
      transform: "translateY(20px)",
    },
    processTitle: {
      fontSize: "clamp(18px, 4vw, 20px)",
      fontWeight: "600",
      color: "#111",
      marginBottom: "20px",
      textAlign: "center",
      textTransform: "lowercase",
    },
    processSteps: {
      display: "flex",
      flexDirection: "column",
      gap: "12px",
    },
    processStep: {
      display: "flex",
      alignItems: "flex-start",
      gap: "12px",
      padding: "16px",
      backgroundColor: "#f8fffe",
      borderRadius: "6px",
      border: "1px solid #00c9a7",
      boxSizing: "border-box",
    },
    stepNumber: {
      fontSize: "14px",
      fontWeight: "600",
      color: "#00c9a7",
      backgroundColor: "white",
      borderRadius: "50%",
      width: "28px",
      height: "28px",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      border: "2px solid #00c9a7",
      flexShrink: "0",
      minWidth: "28px",
    },
    stepText: {
      fontSize: "clamp(14px, 3vw, 16px)",
      color: "#333",
      textTransform: "lowercase",
      lineHeight: "1.4",
      wordWrap: "break-word",
    },
    calculator: {
      marginBottom: "32px",
      padding: "24px 20px",
      backgroundColor: "white",
      borderRadius: "8px",
      border: "2px solid #00c9a7",
      boxSizing: "border-box",
      opacity: 1,
      transform: "translateY(0)",
      transition: "opacity 0.6s ease, transform 0.6s ease",
    },
    calculatorHidden: {
      opacity: 0,
      transform: "translateY(20px)",
    },
    calculatorTitle: {
      fontSize: "clamp(18px, 4vw, 20px)",
      fontWeight: "600",
      color: "#111",
      marginBottom: "20px",
      textAlign: "center",
      textTransform: "lowercase",
    },
    calculatorInputs: {
      display: "grid",
      gridTemplateColumns: "repeat(auto-fit, minmax(100px, 1fr))",
      gap: "12px",
      marginBottom: "20px",
    },
    inputGroup: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      minWidth: "0",
    },
    inputLabel: {
      fontSize: "clamp(12px, 2.5vw, 14px)",
      color: "#666",
      marginBottom: "6px",
      fontWeight: "500",
      textTransform: "lowercase",
      textAlign: "center",
    },
    input: {
      width: "100%",
      maxWidth: "120px",
      padding: "10px 8px",
      border: "1px solid #ddd",
      borderRadius: "6px",
      fontSize: "clamp(14px, 3vw, 16px)",
      textAlign: "center",
      backgroundColor: "white",
      boxSizing: "border-box",
    },
    calculationResult: {
      textAlign: "center",
      padding: "16px",
      backgroundColor: "#f0fdfa",
      borderRadius: "6px",
      border: "1px solid #00c9a7",
      transform: "scale(1)",
      transition: "transform 0.3s ease",
    },
    calculationResultPulse: {
      transform: "scale(1.05)",
    },
    calculationFormula: {
      fontSize: "clamp(14px, 3vw, 16px)",
      color: "#666",
      marginBottom: "8px",
      textTransform: "lowercase",
      wordWrap: "break-word",
    },
    totalCost: {
      fontSize: "clamp(20px, 4vw, 28px)",
      color: "#00c9a7",
      fontWeight: "600",
      wordWrap: "break-word",
    },
    ctaSection: {
      textAlign: "center",
      padding: "0 20px",
      opacity: 1,
      transform: "translateY(0)",
      transition: "opacity 0.6s ease, transform 0.6s ease",
    },
    ctaSectionHidden: {
      opacity: 0,
      transform: "translateY(20px)",
    },
    ctaButton: {
      display: "block",
      width: "100%",
      maxWidth: "280px",
      padding: "16px 24px",
      backgroundColor: "#00c9a7",
      color: "white",
      textDecoration: "none",
      borderRadius: "6px",
      fontSize: "clamp(14px, 3vw, 16px)",
      fontWeight: "500",
      margin: "0 auto 16px auto",
      textTransform: "lowercase",
      boxSizing: "border-box",
      transition: "background-color 0.2s ease",
      cursor: "pointer",
      border: "none",
    },
    ctaButtonHover: {
      backgroundColor: "#00b89a",
    },
    ctaLink: {
      display: "block",
      color: "#00c9a7",
      textDecoration: "none",
      fontSize: "clamp(14px, 3vw, 16px)",
      textTransform: "lowercase",
      marginTop: "8px",
      transition: "color 0.2s ease",
      cursor: "pointer",
    },
    ctaLinkHover: {
      color: "#00b89a",
    },
    stickyCTA: {
      position: "fixed",
      bottom: "20px",
      left: "50%",
      transform: "translateX(-50%)",
      zIndex: 1000,
      opacity: 1,
      transition: "opacity 0.3s ease, transform 0.3s ease",
    },
    stickyCTAHidden: {
      opacity: 0,
      transform: "translateX(-50%) translateY(20px)",
      pointerEvents: "none",
    },
    stickyButton: {
      padding: "12px 24px",
      backgroundColor: "#00c9a7",
      color: "white",
      textDecoration: "none",
      borderRadius: "6px",
      fontSize: "16px",
      fontWeight: "500",
      textTransform: "lowercase",
      boxShadow: "0 4px 12px rgba(0, 201, 167, 0.3)",
      border: "none",
      cursor: "pointer",
      transition: "background-color 0.2s ease, transform 0.2s ease",
    },
    stickyButtonHover: {
      backgroundColor: "#00b89a",
      transform: "translateY(-2px)",
    },
  };

  return (
    <div style={styles.container}>
      {/* 1. Hero Section */}
      <section
        ref={heroRef}
        id="hero"
        style={{
          ...styles.hero,
          ...(visibleSections.has("hero") ? {} : styles.heroHidden),
        }}
      >
        <h1 style={styles.headline}>
          you lose 8 hours every week to operational chaos.
        </h1>
        <h2 style={styles.subhead}>
          meetings about meetings. lost handoffs. tool sprawl. decision
          bottlenecks.
        </h2>
      </section>

      {/* 2. Three Stats */}
      <section
        ref={(el) => {
          sectionRefs.current.stats = el;
        }}
        id="stats"
        style={{
          ...styles.stats,
          ...(visibleSections.has("stats") ? {} : styles.statsHidden),
        }}
      >
        <div style={styles.statCard}>
          <div style={styles.statNumber}>45%</div>
          <div style={styles.statText}>overhead reduction</div>
        </div>
        <div style={styles.statCard}>
          <div style={styles.statNumber}>8hrs</div>
          <div style={styles.statText}>saved per week</div>
        </div>
        <div style={styles.statCard}>
          <div style={styles.statNumber}>$47k</div>
          <div style={styles.statText}>annual savings</div>
        </div>
      </section>

      {/* 3. How it works */}
      <section
        ref={(el) => {
          sectionRefs.current.process = el;
        }}
        id="process"
        style={{
          ...styles.process,
          ...(visibleSections.has("process") ? {} : styles.processHidden),
        }}
      >
        <h3 style={styles.processTitle}>how it works</h3>
        <div style={styles.processSteps}>
          <div style={styles.processStep}>
            <div style={styles.stepNumber}>1</div>
            <div style={styles.stepText}>
              answer 20 binary questions (8 minutes)
            </div>
          </div>
          <div style={styles.processStep}>
            <div style={styles.stepNumber}>2</div>
            <div style={styles.stepText}>
              get your archetype + overhead metrics instantly
            </div>
          </div>
          <div style={styles.processStep}>
            <div style={styles.stepNumber}>3</div>
            <div style={styles.stepText}>
              unlock full diagnostic with quick wins ($495)
            </div>
          </div>
          <div style={styles.processStep}>
            <div style={styles.stepNumber}>4</div>
            <div style={styles.stepText}>
              implement with our guidance (30-min call included)
            </div>
          </div>
        </div>
      </section>

      {/* 4. Simple Calculator */}
      <section
        ref={(el) => {
          sectionRefs.current.calculator = el;
        }}
        id="calculator"
        style={{
          ...styles.calculator,
          ...(visibleSections.has("calculator") ? {} : styles.calculatorHidden),
        }}
      >
        <h3 style={styles.calculatorTitle}>your current overhead cost</h3>
        <div style={styles.calculatorInputs}>
          <div style={styles.inputGroup}>
            <label style={styles.inputLabel} htmlFor="team-size">
              team size
            </label>
            <input
              id="team-size"
              type="number"
              value={teamSize}
              onChange={(e) => setTeamSize(parseInt(e.target.value) || 0)}
              min="1"
              max="50"
              style={styles.input}
              aria-label="Team size"
            />
          </div>
          <div style={styles.inputGroup}>
            <label style={styles.inputLabel} htmlFor="hours-lost">
              hours lost
            </label>
            <input
              id="hours-lost"
              type="number"
              value={hoursLost}
              onChange={(e) => setHoursLost(parseInt(e.target.value) || 0)}
              min="1"
              max="40"
              style={styles.input}
              aria-label="Hours lost per week"
            />
          </div>
          <div style={styles.inputGroup}>
            <label style={styles.inputLabel} htmlFor="hourly-rate">
              hourly rate
            </label>
            <input
              id="hourly-rate"
              type="number"
              value={hourlyRate}
              onChange={(e) => setHourlyRate(parseInt(e.target.value) || 0)}
              min="20"
              max="200"
              style={styles.input}
              aria-label="Hourly rate in dollars"
            />
          </div>
        </div>
        <div
          style={{
            ...styles.calculationResult,
            ...(pulseAnimation ? styles.calculationResultPulse : {}),
          }}
        >
          <div style={styles.calculationFormula}>
            {teamSize}  {hoursLost}  ${hourlyRate}  52 weeks =
          </div>
          <div style={styles.totalCost}>
            ${calculateCost().toLocaleString()}/year in lost productivity
          </div>
        </div>
      </section>

      {/* 5. Two CTAs */}
      <section
        ref={(el) => {
          sectionRefs.current.cta = el;
        }}
        id="cta"
        style={{
          ...styles.ctaSection,
          ...(visibleSections.has("cta") ? {} : styles.ctaSectionHidden),
        }}
      >
        <button
          style={{
            ...styles.ctaButton,
            ...(buttonHovered ? styles.ctaButtonHover : {}),
          }}
          onClick={handleCTAClick}
          onMouseEnter={() => setButtonHovered(true)}
          onMouseLeave={() => setButtonHovered(false)}
          aria-label="Start free assessment"
        >
          start free assessment
        </button>
        <button
          style={{
            ...styles.ctaLink,
            ...(linkHovered ? styles.ctaLinkHover : {}),
            background: "none",
            border: "none",
            padding: "0",
            margin: "8px auto 0 auto",
            display: "block",
          }}
          onClick={handleCTAClick}
          onMouseEnter={() => setLinkHovered(true)}
          onMouseLeave={() => setLinkHovered(false)}
          aria-label="View sample report"
        >
          see sample report
        </button>
      </section>

      {/* Sticky CTA */}
      <div
        style={{
          ...styles.stickyCTA,
          ...(showStickyCTA ? {} : styles.stickyCTAHidden),
        }}
      >
        <button
          style={{
            ...styles.stickyButton,
            ...(buttonHovered ? styles.stickyButtonHover : {}),
          }}
          onClick={handleCTAClick}
          onMouseEnter={() => setButtonHovered(true)}
          onMouseLeave={() => setButtonHovered(false)}
          aria-label="Start free assessment"
        >
          start free assessment
        </button>
      </div>
    </div>
  );
};

export default LandingV2;
